@media screen and (min-width: 1110px) {
  .main {
    display: flex;
    height: 100%;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: center;
    overflow: hidden;
    background-image: linear-gradient(
      to right bottom,
      #ffffff,
      #f8f7fd,
      #e8e8fa,
      #e0e2f7,
      #bcc3e7
    );

    .buttons {
      width: 500px;
      display: flex;
      justify-content: space-around;
    }

    .buttons :hover {
      filter: brightness(110%);
      color: rgb(58, 58, 58);
      text-shadow: rgb(92, 92, 92) 2px 2px 5px;
    }

    p {
      font-family: serif;
      font-weight: bold;
      font-size: 1.5rem;
      margin-bottom: 3rem;
      color: rgb(63, 63, 63);
    }
  }

  .p {
    color: rgb(165, 165, 165);
  }

  .Title {
    font-size: 7rem;
    color: blue;
  }
}
@media screen and (max-width: 1110px) {
  .main {
    min-width: 557px;
    width: 100%;
    height: 100%;
    z-index: -2;
    display: flex;
    flex-direction: column;
    justify-content: space-around;

    text-align: center;

    h2 {
      width: fit-content;
      margin: 20% auto 10% auto;
      font-size: 65px;
      background-color: rgb(53, 52, 52);
      color: white;
      padding: 5px 10px;
    }

    p {
      font-size: 1.5rem;
      width: 40ch;
      color: rgb(53, 52, 52);
      margin: auto;
    }
    .buttons {
      width: 600px;
      margin: 10% auto 10% auto;
      display: flex;
      justify-content: space-around;
    }
    @media screen and (max-width: 638px) {
      .buttons {
        display: flex;
        width: 100%;
        height: 250px;
        flex-direction: column;

        button {
          width: 300px;
          height: 80px;
        }
      }
    }

    .buttons :hover {
      filter: brightness(110%);
      color: rgb(58, 58, 58);
      text-shadow: rgb(92, 92, 92) 2px 2px 5px;
    }
  }
}
