* {
  font-family: Lato;
  font-weight: 700;
}

@media screen and (min-width: 1110px) {
  .body {
    min-width: 700px;
    width: 85vw;
    height: 100%;
    position: fixed;
    top: 0rem;
    padding-left: 15vw;
    overflow-y: scroll;
    font-family: "Lato", sans-serif;
  }

  @media only screen and (max-width: 1000px) {
    .body {
      padding-left: 160px;
    }
  }

  @media only screen and (min-width: 1900px) {
    .body {
      padding-left: 300px;
    }
  }
}
@media screen and (max-width: 1110px) {
  .body {
    min-width: 557px;
    z-index: -5;
  }
}
