$color-header: #4f4f4f;
$color-body-background: #e5e5e5;
$color-nav-background: #ccd0d3;
$color-blue: #2f80ed;
$color-green: #219653;
$btn-orange: #ff8213;
$text-size-nav: 1.5rem;

@media screen and (min-width: 1110px) {
  .aboutGradient {
    width: 100%;
    height: 100%;
    background: rgb(250, 250, 250);
    background: linear-gradient(rgb(255, 255, 255) 0%, rgb(219, 216, 216) 100%);
  }
  .about {
    box-sizing: border-box;
    margin: 0rem;
    padding: 0rem;
    width: 85ch;
    margin: auto;

    .slogan {
      width: fit-content;
      margin: auto;
      color: white;
      padding: 2px 4px;
      background-color: $color-green;
    }
    h2 {
      color: white;
      font-size: 30px;
    }
    h3 {
      color: white;
      font-size: 25px;
    }

    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
    justify-content: center;

    .section-info {
      display: flex;
      justify-content: center;
      flex-direction: column;
    }

    label {
      text-align: left;
    }

    p {
      color: $color-header;
      font-size: 20px;
    }
    .cards {
      width: 85ch;
      h3 {
        background-color: $color-blue;
        font-size: 30px;
        color: white;
        padding: 3px 5px;
      }

      p {
        margin: auto;
        width: auto;
        background-color: $color-blue;
        font-size: 30px;
        color: white;
        text-align: center;
        padding: 3px 5px;
      }
    }
    .cards {
      display: flex;
      align-items: center;
    }
    .contact {
      display: flex;
      width: 800px;
      justify-content: space-around;
      align-items: center;
      a {
        text-decoration: none;
        color: $color-header;
        p {
          background-color: rgb(32, 32, 32);
          color: white;
          font-weight: 700;
          font-size: 1.5rem;
          padding: 0rem 10px;
        }
      }
      img {
        opacity: 1;
      }
      h2 {
        color: rgb(10, 10, 10);
      }
      a:hover {
        h2 {
          color: rgb(66, 66, 66);
        }
        img {
          opacity: 0.8;
        }
      }

      img {
        width: 5rem;
        height: auto;
      }
      iframe {
        width: 30rem;
        height: 12rem;
      }
    }
  }
}

//responsive
@media screen and (max-width: 1110px) {
  .aboutGradient {
    width: 100%;
  }
  .about {
    box-sizing: border-box;
    margin: 0rem;
    padding: 0rem;
    margin: auto;

    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
    justify-content: center;

    .section-info {
      display: flex;
      justify-content: center;
      flex-direction: column;
    }
    .slogan {
      width: fit-content;
      margin: auto;
      color: $color-header;
      padding: 2px 4px;
    }
    h1 {
      font-size: 50px;
    }
    h2 {
      color: white;
      font-size: 20px;
    }
    h3 {
      color: white;
      font-size: 20px;
    }

    label {
      text-align: left;
    }

    p {
      color: $color-header;
      font-size: 20px;
      width: 90%;
      margin: 2rem auto;
    }
    .cards {
      width: 90%;
      margin: 1rem auto;
      h3 {
        font-size: 30px;
        width: fit-content;
        margin: auto;
        background-color: $color-blue;
        padding: 3px 5px;
      }

      p {
        width: auto;
        font-size: 30px;
        background-color: $color-blue;
        text-align: center;
        padding: 3px 5px;
        color: white;
      }
    }
    .contact {
      display: flex;
      width: 100%;
      justify-content: space-around;
      align-items: center;
      a {
        text-decoration: none;
        color: $color-header;
        p {
          background-color: rgb(32, 32, 32);
          color: white;
          font-weight: 700;
          font-size: 1.5rem;
          padding: 0rem 10px;
        }
      }
      img {
        opacity: 1;
      }
      h2 {
        color: rgb(10, 10, 10);
      }
      a:hover {
        h2 {
          color: rgb(66, 66, 66);
        }
        img {
          opacity: 0.8;
        }
      }

      img {
        width: 5rem;
        height: auto;
      }
      iframe {
        width: 400px;
        height: 200px;
      }
    }
  }
}
