@import "../../style/global";

.checkout {
  display: flex;
  justify-content: space-between;
  height: inherit;
  width: 100%;

  .checkoutList {
    margin: auto;
    text-align: center;
    flex-basis: 40rem;
    align-items: flex-start;
    overflow: scroll;
    height: 100vh;

    .total {
      background-color: rgb(224, 224, 224);
      width: 80%;
      margin: auto;
      display: flex;
      font-size: 28px;
      justify-content: center;
      height: 60px;
      align-items: center;
      margin-bottom: 10px;
      cursor: default;
      color: $color-header;

      h3 {
        color: rgb(58, 168, 67);
      }
    }
  }

  .form {
    flex: 2 1 40%;
    background-color: grey;
    flex-basis: 20rem;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(218, 218, 218);

    h2 {
      font-size: 3rem;
      color: $color-header;
    }

    .button {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 1rem 2rem;
      width: 20rem;
      height: 5rem;
      margin: auto;
      box-sizing: border-box;

      h3 {
        width: 100%;
        padding: 1rem 2rem;
        text-align: center;
        color: honeydew;
        font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
          Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
        font-size: 1.5rem;
        font-weight: lighter;
        background-color: $color-blue;
      }
    }

    .button :hover {
      cursor: pointer;
      filter: brightness(120%);
      color: rgb(216, 216, 216);
    }
    p {
      font-weight: 500;
      font-size: 20px;
      color: rgb(63, 63, 63);
      text-align: center;
      margin: auto;
    }
  }
}
@media screen and (max-width: 1110px) {
  .checkout {
    display: block;
    .checkoutList {
      margin-bottom: 2rem;
      width: 100%;
      height: auto;
      overflow: visible;
    }
    .form {
      width: 100%;
    }
  }
}
