@import "../../style/global";
@media screen and (min-width: 1110px) {
  .navContainer {
    display: flex;
    position: sticky;
    left: 0rem;
    top: 0rem;
    width: 15vw;
    height: 100vh;
    overflow: hidden;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    background-color: rgb(39, 39, 39);
    z-index: 100;

    a {
      text-decoration: none;
    }

    img {
      object-fit: contain;
      width: inherit;
      min-width: 160px;
      max-width: 300px;
      height: auto;
      padding-left: 10px;
      background-color: rgb(233, 233, 233);
    }

    nav {
      display: flex;
      height: 70%;
      flex-direction: column;
      justify-content: space-around;
      font-family: Lato;
      font-weight: 300;
    }

    .nav-items {
      color: antiquewhite;
      width: inherit;
      height: max-content;
      list-style: none;

      p {
        font-size: 2rem;
        padding: 10px 20vw;
        font-family: Lato;
        font-weight: 300;
      }
    }

    .nav-items :hover {
      transition: 20ms;
      color: whitesmoke;
      filter: brightness(110%);
    }
  }
}
@media screen and (max-width: 1110px) {
  .navContainer {
    min-width: 557px;
    box-sizing: border-box;
    top: 0rem;
    width: 100%;
    flex-direction: column;
    display: flex;
    font-size: 20px;
    background-color: rgb(51, 51, 51);
    z-index: 100;
    a {
      color: white;
      text-decoration: none;
      height: auto;
      list-style: none;
    }
    img {
      width: 200px;
      height: auto;
      margin: 0rem auto 1rem auto;
    }
    nav {
      display: flex;
      width: 100%;
      justify-content: space-around;
    }
    .nav-items {
      width: auto;
      padding: 10px 30px;
      margin: 10px;
      border-radius: 10rem;
    }
  }
}
