.shop {

    h2 {
        text-align: center;
    }

    .products {
        width: inherit;

        .product-items {
            width: 80%;
            height: 150px;
            display: flex;
            justify-content: space-around;
            align-items: center;
            margin: auto;
            margin-top: 1rem;
            background-color: rgb(223, 223, 223);
            cursor: default;

            .items-value {

                h3 {
                    font-size: 25px;
                    color: #1f1f1f;
                }

                p {
                    font-family: Lato;
                    font-weight: 400;
                    font-size: 22px;
                    color: #727272;
                }
            }

            .buy-button p {
                margin: 10px;
                padding: 20px 40px;
                color: white;
                font-size: 20px;
                background-color: #1ca357;
                cursor: pointer;
            }

            .buy-button .Active {
                background-color: #116d39;
            }

        }
    }

    .buy-button p :hover,
    p:active {
        color: black;
        filter: brightness(120%);
        cursor: pointer;
    }
}