.cartItem {
    background-color: rgb(224, 224, 224);
    width: 80%;
    margin: auto;
    display: flex;
    justify-content: space-around;
    height: 60px;
    align-items: center;
    margin-bottom: 10px;
    cursor: default;

    .btns {
        display: flex;
        align-items: center;
        width: 50px;
        justify-content: space-between;
        font-size: 25px;

        div {
            background-color: #219653;
            width: 20px;
            text-align: center;
            border-radius: 5px;
            color: white;
            padding: 5px 5px;
            margin: 0px 5px;
            cursor: pointer;
        }
    }

    .name {
        width: 150px;
        color: rgb(22, 22, 22);
        font-size: 20px;
    }

    .price {
        width: 80px;
        color: rgb(18, 70, 51);
        font-size: 25px;
    }

}